import React from 'react';
import './Loading.scss';
import {AiOutlineLoading3Quarters} from 'react-icons/ai';

const Loading = () => {
    return (
        <div className="loadWrap">
            <div className="icon"><AiOutlineLoading3Quarters size="50" /></div>
            <p>Loading...</p>
        </div>
    );
};

export default Loading;