import React from 'react';
import './Movies.scss';
import {Link} from 'react-router-dom';

function Movies({year, title, summary, poster, genres}) { 
        return (
            <Link to={{
                pathname:'/detail',
                state:{
                    year:year,   //앞에는 사용자지정 : 뒤에는 프롭스로 전달받은 이름
                    title,  // 키값과 데이터값이 같으면 줄여서 사용가능
                    summary,
                    genres,
                    poster
                }
            }}>
                <div className="movies">
                    <img src={poster} alt={title} />
                    <div className="movies_data">
                        <h3 className="movies_title">{title}</h3>
                        <h5 className="movies_year">{year}</h5>
                        <ul>
                            {
                                genres.map((data, i)=>(
                                    <li key={i}>/ {data} </li>
                                ))
                            }
                        </ul>
                        <p className="movies_summary">{summary.slice(0,100)}...</p>
                    </div>
                </div>
            </Link>
        );
}

export default Movies;
