import React, { Component } from 'react';
import Movies from './Movies';
import Loading from './Loading';
import axios from 'axios';
import './Section.scss';

class Section extends Component {
    state = {  //  movies라는 키를 가진 객체{}를 생성한다. 
        kim : [],   // 대열변수로 키를 만든다.
        isLoading : true  //키값을 true로
    }

    getMovies = async () => { //비동기식 방식으로 오픈API를 가지고 온다. api의 형식이 객체형식이므로 {}해준다. {}안에는 키값이 
        const { data : { data : { movies }}} = await axios.get("https://yts.mx/api/v2/list_movies.json")
        this.setState({
            kim : movies // state에 kim(key)가 movies로수정이 된다. 이후 render()가 호출된다.
        })
    }

    //reder() 수행 후 한번만 호출되는 함수 componentDidMount()
    componentDidMount() {
        //setTimeout(함수,5000)함수를 이용해 5초후에 이즈로딩을 fales로 바꾼다.
        setTimeout( ()=>{
            this.setState({ //state값을 수정하기위해 setState함수 호출
                isLoading : false
            })
        },2000);
        this.getMovies()
    }
    
    render() {
        //비구조화 할당으로 변경
        const { kim, isLoading } = this.state;
        return (
            <>
            {   //// 삼한조건연산자 이즈로딩이 true이면 : 앞에있는 "Loading"을 실행하고 false면 : 뒤에가 실행
                isLoading ? <Loading /> : (
                    <section className="container">
                    {
                        kim.map((data)=>(
                            <Movies key={data.id} year={data.year} title={data.title} summary={data.summary} poster={data.medium_cover_image} genres={data.genres} />
                        ))
                    }
                    </section>
                )
            }
            </>

        );
    }
}

export default Section;

// 코딩 순서도
// -> render() 함수가 실행이 된다.
// -> componenetDidMount() 함수가 있다면 이함수를 읽게된다.
// -> setState() 
// -> render()